import React, { Component } from 'react';
import { Card, CardBody, CardHeader, Col, Row, Table } from 'reactstrap';
import service from '../../_services/user-question.service';

class User extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: []
    }
  }

  async getData() {
    let link = this.props.match.params.id;
    await service.getQuestionsAndAnswers(link).then(res => {
      this.setState({
        data: res.data
      })
    }).catch(err => {
      console.log(err);

    })
  }
  async componentDidMount() {
    await this.getData();
  }
  render() {

    const user = this.state.data;

    // const userDetails = user ? Array.fill(user) : [['id', (<span><i className="text-muted icon-ban"></i> Not found</span>)]]

    return (
      <div className="animated fadeIn">
        <Row>
          <Col lg={12}>
            <Card>
              <CardHeader>
                <strong><i className="icon-info pr-1"></i>Quiz id: {this.props.match.params.id}</strong>
              </CardHeader>
              <CardBody>
                <Table responsive striped hover>
                <thead>
                    <tr>
                      <th scope="col">id</th>
                      <th scope="col">Question</th>
                      <th scope="col"> Answer</th>

                    </tr>
                  </thead>
                  <tbody>
                    {
                      user.map((data, index) => {
                        return (
                          <tr key={index}>
                            <td>{`${index+1}:`}</td>
                            <td><strong>{data.question_text}</strong></td>
                            <td><strong>{data.option}</strong></td>

                          </tr>
                        )
                      })
                    }
                  </tbody>
                </Table>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    )
  }
}

export default User;
